/* global Parse */
import React from "react";
import rtClient from '../library/Realtime'
import Parse from '../library/parse'

class Home extends React.Component {

    componentDidMount() {
        const { onCreateEvent } = this.props;
        rtClient.open()
        const query = new Parse.Query('Event');
        query.ascending('createdAt').limit(5);
        const subscription = rtClient.subscribe(query);
        const handleWS = (event) => {
            onCreateEvent && onCreateEvent(event)
        }
        subscription.on('create', handleWS);
    }
    render() {
        return <></>
    }
} 


export default Home;
